// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import { Table, TableColumn } from 'element-ui'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  async asyncData({ store, $company, error }) {
    await $company
      .getCompanies()
      .then(data => {
        store.commit('company/setCompanies', data)
      })
      .catch(err => {
        error({
          statusCode: err.status || 404,
          message: err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message
        })
      })
  },
  data() {
    return {
      loading: false
    }
  },
  computed: {
    ...mapState({
      companies: state => state.company.companies.data,
      meta: state => state.company.companies.meta,
      links: state => state.company.companies.links
    })
  },
  methods: {
    next() {
      const { next } = this.links
      this.$company
        .getcompanies(next)
        .then(({ data }) => {
          store.commit('company/setCompanies', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    prev() {
      const { prev } = this.links

      this.$company
        .getcompanies(prev)
        .then(({ data }) => {
          store.commit('company/setCompanies', data)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    removeCompany(companyId) {
      if (!confirm('Are you sure ? ')) return
      this.loading = !this.loading

      this.$company
        .removeOneCompany(companyId)
        .then(res => {
          this.$store.commit('company/removeOneCompany', companyId)
          this.loading = !this.loading
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
          this.loading = !this.loading
        })
    }
  }
}
